* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #333;
  background-color: #f5f5f5;
  padding: 20px;
  font-family: Helvetica Neue, Arial, sans-serif;
  line-height: 1.6;
}

.container {
  background-color: #fff;
  border-radius: 10px;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 2px 10px #0000001a;
}

h1 {
  text-align: center;
  color: #2c3e50;
  margin-bottom: 20px;
}

.stats {
  background-color: #f9f9f9;
  border-radius: 5px;
  justify-content: space-between;
  margin-bottom: 20px;
  padding: 10px;
  display: flex;
}

.stats div {
  text-align: center;
}

.word-card {
  background-color: #fff;
  border-radius: 8px;
  margin-bottom: 20px;
  padding: 20px;
  box-shadow: 0 2px 5px #0000001a;
}

.word {
  text-align: center;
  color: #2c3e50;
  margin-bottom: 20px;
  font-size: 28px;
  font-weight: bold;
}

.options {
  grid-template-columns: 1fr;
  gap: 10px;
  margin-bottom: 20px;
  display: grid;
}

.option {
  cursor: pointer;
  background-color: #f8f9fa;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 15px;
  transition: all .2s;
}

.option:hover {
  background-color: #e9ecef;
}

.option.selected {
  background-color: #cce5ff;
  border-color: #b8daff;
}

.option.correct {
  background-color: #d4edda;
  border-color: #c3e6cb;
}

.option.incorrect {
  background-color: #f8d7da;
  border-color: #f5c6cb;
}

.btn {
  color: #fff;
  cursor: pointer;
  background-color: #4a89dc;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
  padding: 10px 20px;
  font-size: 16px;
  display: inline-block;
}

.btn:hover {
  background-color: #3b7dd8;
}

.btn-next {
  background-color: #28a745;
}

.btn-next:hover {
  background-color: #218838;
}

.buttons {
  justify-content: space-between;
  margin-top: 20px;
  display: flex;
}

.password-section {
  background-color: #f9f9f9;
  border-radius: 5px;
  margin-top: 30px;
  padding: 20px;
}

.password-section h3 {
  margin-bottom: 10px;
}

.password-container {
  margin-bottom: 10px;
  display: flex;
}

.password-section input {
  border: 1px solid #ddd;
  border-radius: 5px 0 0 5px;
  flex: 1;
  padding: 10px;
  font-family: monospace;
}

.btn-copy {
  background-color: #6c757d;
  border-radius: 0 5px 5px 0;
  margin-right: 0;
}

.btn-copy:hover {
  background-color: #5a6268;
}

.copy-message {
  color: #155724;
  text-align: center;
  background-color: #d4edda;
  border-radius: 3px;
  margin-bottom: 10px;
  padding: 5px;
  font-size: 14px;
}

.password-buttons {
  justify-content: space-between;
  display: flex;
}

.btn-danger {
  background-color: #dc3545;
}

.btn-danger:hover {
  background-color: #c82333;
}

@media (width >= 576px) {
  .options {
    grid-template-columns: 1fr 1fr;
  }
}

.statistics {
  background-color: #f8f9fa;
  border-radius: 8px;
  margin-bottom: 20px;
  padding: 20px;
  box-shadow: 0 1px 3px #0000001a;
}

.statistics h3 {
  color: #2c3e50;
  text-align: center;
  margin-bottom: 15px;
}

.stats-container {
  justify-content: space-around;
  margin-bottom: 15px;
  display: flex;
}

.stat-item {
  text-align: center;
}

.stat-value {
  color: #2c3e50;
  font-size: 24px;
  font-weight: bold;
}

.stat-label {
  color: #6c757d;
  font-size: 14px;
}

.stats-details {
  color: #6c757d;
  justify-content: space-between;
  margin-top: 15px;
  font-size: 14px;
  display: flex;
}

.detail-label {
  font-weight: bold;
}

.result-message {
  color: #28a745;
  margin: 15px 0;
  font-size: 20px;
  font-weight: bold;
}

.result-stats {
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 20px 0;
  display: flex;
}

.result-stat-box {
  background-color: #fff;
  border-radius: 8px;
  min-width: 200px;
  margin: 10px;
  padding: 15px;
  box-shadow: 0 2px 5px #0000001a;
}

.result-stat-value {
  color: #2c3e50;
  font-size: 28px;
  font-weight: bold;
}

.result-stat-label {
  color: #6c757d;
  margin-bottom: 10px;
}

.result-stat-percentage {
  color: #28a745;
  margin-top: 5px;
  font-weight: bold;
}

.mini-progress-bar {
  background-color: #e9ecef;
  border-radius: 5px;
  height: 10px;
  margin: 5px 0;
  overflow: hidden;
}

.mini-progress-fill {
  background-color: #28a745;
  height: 100%;
  transition: width .3s;
}

.completion-message {
  color: #155724;
  background-color: #d4edda;
  border-radius: 5px;
  margin: 20px 0;
  padding: 15px;
}

.remaining-words {
  color: #6c757d;
  margin: 15px 0;
  font-weight: bold;
}

.btn-large {
  margin-top: 15px;
  padding: 12px 30px;
  font-size: 18px;
}

.answer-result {
  text-align: center;
  margin: 15px 0;
}

.correct-message {
  color: #28a745;
  background-color: #d4edda;
  border-radius: 5px;
  padding: 10px;
  font-size: 18px;
  font-weight: bold;
}

.incorrect-message {
  color: #dc3545;
  background-color: #f8d7da;
  border-radius: 5px;
  padding: 10px;
  font-size: 18px;
  font-weight: bold;
}

.incorrect-message p:last-child {
  color: #155724;
  margin-top: 5px;
  font-size: 16px;
}

.app-header {
  border-bottom: 1px solid #e9ecef;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 15px;
  display: flex;
}

.app-title {
  color: #2c3e50;
  margin: 0;
  font-size: 24px;
}

.app-footer {
  text-align: center;
  color: #6c757d;
  border-top: 1px solid #e9ecef;
  margin-top: 30px;
  padding-top: 15px;
  font-size: 14px;
}
